import {uniPopup} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			form: {
				remark: ''
			}
		}
	},
	components: {
		uniPopup
	},
	props: {
		value: {
			type: null
		}
	},
	watch: {
	},
	methods: {
		submit() {
			uni.showLoading()
			let form = Object.assign({order_sn: this.value}, this.form)
			this.$http.post('order.apply_refund', form).then(res => {
				uni.hideLoading()
				this.close()
				this.$emit('callback')
			}).catch(e => {
				uni.hideLoading()
			})
		},
		open() {
			this.$refs.$popup.open()
		},
		close() {
			this.$refs.$popup.close()
		}
	}
}
